.swoosh-animation{
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: 169px;
  background-image: url(/static/media/sparkle.19e97c5f.png);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: 120%;
  z-index: 10;
  -webkit-animation: swoosh 2s steps(45) 1;
          animation: swoosh 2s steps(45) 1;
}

@-webkit-keyframes swoosh{
  from{background-position: top;}
  to{background-position: bottom;}
}

@keyframes swoosh{
  from{background-position: top;}
  to{background-position: bottom;}
}

@-webkit-keyframes fade-in{
  from{opacity: 0%}
  to{opacity: 100%}
}

@keyframes fade-in{
  from{opacity: 0%}
  to{opacity: 100%}
}

