.card-wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 400px;
}

.outside-wrapper{
    position: relative;
    margin-bottom: 20px;
}

.experience-card-container{
    position: relative;
    z-index: 5;
    width: 250px;
    height: 200px;
    background-color: #f0e9e8;
}

.card-topper{
    position: relative; 
    z-index: 5;
    height: 40px;
    width: 100%;
    background-color: #aa3281;
}

.card-intro{
    position: absolute;
    z-index: 5;
    height: 160px;
    width: 100%;
}

.expanded-card{
    position: absolute;
    top: 2px;
    height: 400px;
    width: 251px;
    z-index: 10;
    -webkit-perspective: 2000px;
            perspective: 2000px;
    -webkit-perspective-origin: 0 50%;
            perspective-origin: 0 50%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotateX(-160deg);
            transform: rotateX(-160deg);
    -webkit-animation: unfold 1s;
            animation: unfold 1s;
}

@-webkit-keyframes unfold {
    from{-webkit-transform: rotateX(-70deg);transform: rotateX(-70deg)}
    to{-webkit-transform: rotateX(-160deg);transform: rotateX(-160deg)}
}

@keyframes unfold {
    from{-webkit-transform: rotateX(-70deg);transform: rotateX(-70deg)}
    to{-webkit-transform: rotateX(-160deg);transform: rotateX(-160deg)}
}

.card-section{
    position: absolute;
    width: 100%;
    height: 200px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.expanded-card-front{
    background-color: #fcfcfc;
}

.expanded-card-back{
    background-image: linear-gradient(#e0d3d1, #fcfbfa);
    -webkit-transform: rotateX(175deg);
            transform: rotateX(175deg);
    box-shadow: 5px 5px 5px rgba(0,0,0,.4);
}

.card-topper img{
    height: 100%;
}

.card-intro img{
    height: 100%;
    width: 100%;
}

.card-title{
    height: 40px;
    position: absolute;
    bottom: 0;
